<template>
    <div class="location-notes">
        <div id="notes" class="collapse in">
            <div class="form-horizontal form-widgets">
                <div class="form-group">
                    <div class="wrapper wrapper-content animated fadeInUp">
                        <kendo-listview :data-source-ref="'datasourceLocationNotes'"
                                        :template="noteListTemplate">
                        </kendo-listview>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import axios from "axios";
    export default {
        name: 'location-notes',
        data() {
            return {
                datasourceLocationNotes:[],
                LocationId:'',
                noteListTemplate: `
                 <li>
                    <div>
                        <small>#:kendo.format('{0:d}', kendo.parseDate(DatetimeCreated))# #:CreatedByUserFirstName# #:CreatedByUserLastName#</small>
                        <p class="m-t-sm">#:Content#</p>
                        #if (IsOwner == 1) {#
                        <a class="delete" title="Delete note." data-toggle="tooltip" :onclick="deleteNote(#:NoteId#)"><i class="fa fa-trash-o "></i></a>
                        #}#
                        #if (IsPrivate == 1) {# <a class="secret" title="Private note." data-toggle="tooltip"><i class="fa fa-user-secret"></i></a> #}#
                    </div>
                </li>`
            }
        },
        methods: {
            setDefaultValues(locationId) {
                var app = this;
                this.LocationId = locationId;
                app.LoadNotesGridData();
            },
            LoadNotesGridData() {
                var _url = Axios_Get_Url('NotesDetailsByLocation_Read_Vue', 'Territory', { LocationId: this.LocationId }, [])
                axios({ method: "GET", "url": _url }).then(result => {
                    this.datasourceLocationNotes = result.data;
                }, error => {
                    console.error(error);
                });
            }
        },
        mounted() {
        }
    }
</script>

