import Vue from 'vue'
import App from './App'
//import axios from 'axios'
//import '@progress/kendo-theme-default/dist/all.css'
//import '@progress/kendo-ui/js/kendo.treeview'
import { DropDownList, DropdownsInstaller } from '@progress/kendo-dropdowns-vue-wrapper'
import { DatePicker, DateinputsInstaller } from '@progress/kendo-dateinputs-vue-wrapper';
import { DataSourceInstaller } from '@progress/kendo-datasource-vue-wrapper';
import { Grid, GridInstaller } from '@progress/kendo-grid-vue-wrapper'
import { Window, WindowInstaller } from '@progress/kendo-window-vue-wrapper'
import { TreeView, TreeViewItem, TreeViewInstaller } from '@progress/kendo-treeview-vue-wrapper'
import { TabStrip, LayoutInstaller } from '@progress/kendo-layout-vue-wrapper'
import {
    Pager,
    ListView,
    ListViewInstaller
} from '@progress/kendo-listview-vue-wrapper'

Vue.config.productionTip = false

Vue.use(LayoutInstaller);
Vue.use(DropdownsInstaller)
Vue.use(DateinputsInstaller);
Vue.use(GridInstaller)
Vue.use(DataSourceInstaller)
Vue.use(DatePicker)
Vue.use(WindowInstaller)
Vue.use(TreeViewInstaller)
Vue.use(ListViewInstaller)

//axios.defaults.baseURL = 'http://localhost:61224/api';
new Vue({
    el: '#app',
    template: '<App/>',
    components: {
        App,
        DropDownList,
        Grid,
        DatePicker,
        Pager,
        ListView
    }
})