<template>
    <div class="location-grid">
        <input type="button" @click="AddNewLocation" value="Add New" class="btn btn-default pull-right" style="margin-right:15px;margin-bottom:10px;" />
        <div class="col-lg-12 col-md-12">
            
            <kendo-datasource ref="datasource1"
                              :data="gridData"
                              :schema-model-id="'Id'"
                              :schema-model-fields="schemaModelFields"
                              :batch='true'
                              :page-size='20'>
            </kendo-datasource>


            <kendo-grid ref="locationGrid" :height="600"
                        :data-source-ref="'datasource1'"
                        :pageable='true'
                        :selectable='true'
                        :filterable='true'
                        :resizable='true'
                        v-on:change="onLocationGridChange">
                <kendo-grid-column :field="'Id'"></kendo-grid-column>
                <kendo-grid-column :field="'Name'"></kendo-grid-column>
                <kendo-grid-column :field="'Breadcrumb'"></kendo-grid-column>
                <kendo-grid-column :command="{ text: 'View Details', click: showDetails }"
                                   :title="'Action'"></kendo-grid-column>
            </kendo-grid>
        </div>
        <div>
            <location-units v-model="modalOpen" @LocationUpdated="onLocationUpdated" ref="Comp_locationUnits"></location-units>
        </div>
    </div>
</template>

<script>
    import axios from "axios";
    import LocationUnits from './LocationUnits'
    export default {
        name: 'location-grid',
        components: {
            LocationUnits
        },
        data() {
            return {
                schemaModelFields: {
                    Id: { editable: false, nullable: true },
                    Name: { validation: { required: true } },
                    Breadcrumb: { validation: { required: true} }
                },
                gridData: [],
                modalOpen: false
            }
        },
        methods: {
            showDetails: function (e) {
                this.modalOpen = true;
                e.preventDefault();
                var grid = this.$refs.locationGrid.kendoWidget();
                var dataItem = grid.dataItem($(e.currentTarget).closest("tr"));
                this.$refs.Comp_locationUnits.isEditLocation = true;
                this.$refs.Comp_locationUnits.refreshUnits(dataItem.Id);
            },
            AddNewLocation() {
                this.modalOpen = true;
                this.$refs.Comp_locationUnits.isEditLocation = false;
            },
            onLocationUpdated(isLocationUpdated) {
                if (isLocationUpdated) 
                    this.LoadLocations();
            },
            onLocationGridChange(e) {
                var grid = e.sender;
                var selectedRows = grid.select();
                if (selectedRows.length > 0) {
                    var dataItem = grid.dataItem(selectedRows[0]);
                    //alert(dataItem.Id);
                    this.$refs.Comp_locationUnits.isEditLocation = true;
                    this.$refs.Comp_locationUnits.refreshUnits(dataItem.Id);
                }
            },
            LoadLocations() {
                //var _url = Axios_Get_Url('Locations_Read_By_TenantId_Vue', 'Territory', {}, ['TenantId']);
                //axios({ method: "GET", "url": _url }).then(result => {
                //    this.gridData = result.data;
                //}, error => {
                //    console.error(error);
                //});

                var _url = Axios_Get_Url_API('GetLocation', 'Location', { tenantId: func_getTenantId(), userId: func_getUserId() });
                axios({ method: "GET", "url": _url }).then(result => {
                    console.log(result);
                    this.gridData = result.data;
                }, error => {
                    console.error(error);
                });
            }
        },
        mounted() {
            this.LoadLocations();
        }
    }
</script>

