<template>
    <div class="LocationPopupCls" v-show="value">
        <div class="LocationContainer" style="display: inline-grid;">
            <div class="title" id="title">
                <h4 name="Title" class="LocationTitle">
                    Location Details
                </h4>
                <a role="button" @click.prevent="close" href="javascript:;" class="k-window-action k-link" aria-label="Close" style="float: right;">
                    <span class="k-icon k-i-close"></span>
                </a>
            </div>
            <div>
                <kendo-tabstrip>
                    <ul>
                        <li class="k-state-active">
                            Location
                        </li>
                        <li>
                            Contacts
                        </li>
                        <!--<li>
                            Notes
                        </li>-->
                    </ul>
                    <div>
                        <div name="ActualContent" class="content-area">
                            <div class="well k-content">
                                <div class="form-widgets">
                                    <div class="col-lg-5 col-md-5">
                                        <!-- Territory -->
                                        <div class="form-group margin-top">
                                            <label class="control-label col-lg-4 col-md-4 padding0">Territory</label>
                                            <span id="TerritoryName">{{ParentName}}</span>&nbsp;&nbsp;&nbsp;<span id="TerritoryLink"><a class="p-xs" @click="openTerritoryWin" title="Select a Territory" data-toggle="tooltip"><i class="fa fa-pencil-alt"></i></a><a id="removeFromTerritoryButton" class="p-xs" title="Remove from Territory" @click="removeFromTerritory" data-toggle="tooltip" v-show="isShowRemoveTerritoryHidden"><i class="fa fa-times"></i></a></span>
                                        </div>
                                        <!-- Location Name -->
                                        <div class="form-group margin-top">
                                            <label class="control-label col-lg-4 col-md-4 padding0" for="LocationName">Name</label>
                                            <div class="col-lg-8 col-md-8 padding0">
                                                <input type="text" class="form-control" v-model="LocationName" tabindex="1" />
                                            </div>
                                        </div>
                                        <br />
                                        <!-- Is Active -->
                                        <div class="form-group checkbox checkbox-primary margin-top padding0" style="padding-left:16px;">
                                            <input id="LocationIsActive" v-model="LocationIsActive" tabindex="2" type="checkbox" checked />
                                            <label class="control-label m-l-xs" for="LocationIsActive">Is Active</label>
                                        </div>
                                        <!-- Closed Date -->
                                        <div class="form-group margin-top">
                                            <label class="control-label col-lg-4 col-md-4 padding0" for="DateClosed">Closed</label>
                                            <div class="col-lg-8 col-md-8 padding0">
                                                <kendo-datepicker v-model="DateClosed"
                                                                  :value="currentDate"
                                                                  :format="'MM/dd/yyyy'">
                                                </kendo-datepicker>
                                            </div>
                                        </div>
                                        <br />
                                        <!-- Closed By -->
                                        <div class="form-group margin-top">
                                            <label class="control-label col-lg-4 col-md-4 padding0">Closed By</label>
                                            <div id="ClosedByName" class="col-lg-8 col-md-8 padding0">{{ClosedByName}}</div>
                                        </div>
                                        <br />
                                        <!-- Address -->
                                        <div class="form-group margin-top">
                                            <label class="control-label col-lg-4 col-md-4 padding0" for="Address1">Address</label>
                                            <div class="col-lg-8 col-md-8 padding0">
                                                <input type="text" class="form-control" style="width:auto" tabindex="7" v-model="Address1" />
                                            </div>
                                        </div>
                                        <br />
                                        <div class="form-group margin-top">
                                            <label class="control-label col-lg-4 col-md-4 padding0" for="Address2"></label>
                                            <div class="col-lg-8 col-md-8 padding0">
                                                <input type="text" class="form-control" style="width:auto" tabindex="8" v-model="Address2" />
                                            </div>
                                        </div>
                                        <br />
                                        <!-- City -->
                                        <div class="form-group margin-top">
                                            <label class="control-label col-lg-4 col-md-4 padding0" for="City">City</label>
                                            <div class="col-lg-8 col-md-8 padding0">
                                                <input type="text" class="form-control" tabindex="9" v-model="City" />
                                            </div>
                                        </div>
                                        <br />
                                        <!-- Postal Code -->
                                        <div class="form-group margin-top">
                                            <label class="control-label col-lg-4 col-md-4 padding0" for="PostalCode">Postal Code</label>
                                            <div class="col-lg-8 col-md-8 padding0">
                                                <input type="text" class="form-control" tabindex="11" v-model="PostalCode" />
                                            </div>
                                        </div>
                                        <br />
                                    </div>
                                    <div class="col-lg-6 col-md-6">
                                        <div class="form-group margin-top">
                                            <label class="control-label col-lg-4 col-md-4 padding0" for="LocationNbr">Location Nbr</label>
                                            <div class="col-lg-8 col-md-8 padding0">
                                                <input type="text" name="LocationNbr" v-model="LocationNbr" class="form-control" tabindex="2" />
                                            </div>
                                        </div>
                                        <br />
                                        <!-- Description-->
                                        <div class="form-group margin-top">
                                            <label class="control-label col-lg-4 col-md-4 padding0" for="Description">Description</label>
                                            <div class="col-lg-8 col-md-8 padding0">
                                                <input type="text" name="LocationNbr" v-model="Description" class="form-control" tabindex="2" />
                                            </div>
                                        </div>
                                        <br />
                                        <!-- Manager -->
                                        <div class="form-group margin-top">
                                            <label class="control-label col-lg-4 col-md-4 padding0" for="ManagerId">Manager</label>
                                            <div class="col-lg-8 col-md-8 padding0">
                                                <kendo-dropdownlist ref="drpdwnManager"
                                                                    v-model="ManagerId"
                                                                    :data-source="dataSourceArrayManager"
                                                                    :data-text-field="'Name'"
                                                                    :data-value-field="'Id'"
                                                                    :option-label="'Not Specified'"
                                                                    :enable="false">
                                                </kendo-dropdownlist>
                                            </div>
                                        </div>
                                        <br />
                                        <!-- Wynne -->
                                        <div class="form-group margin-top">
                                            <label class="control-label col-lg-4 col-md-4 padding0" for="WynneNbr">Wynne #</label>
                                            <div class="col-lg-8 col-md-8 padding0">
                                                <input type="text" v-model="WynneNbr" name="WynneNbr" class="form-control" tabindex="5" />
                                            </div>
                                        </div>
                                        <br />
                                        <!-- Lawson -->
                                        <div class="form-group margin-top">
                                            <label class="control-label col-lg-4 col-md-4 padding0" for="Lawson">Lawson</label>
                                            <div class="col-lg-8 col-md-8 padding0">
                                                <input type="text" v-model="Lawson" name="Lawson" class="form-control" tabindex="6" />
                                            </div>
                                        </div>
                                        <br />
                                        <!-- State -->
                                        <div class="form-group margin-top" style="margin-top:84px">
                                            <label class="control-label col-lg-4 col-md-4 padding0" for="StateOrProvinceId">State</label>
                                            <div class="col-lg-8 col-md-8 padding0">
                                                <kendo-dropdownlist v-model="StateOrProvinceId"
                                                                    :data-source="dataSourceArrayStates"
                                                                    :data-text-field="'Name'"
                                                                    :data-value-field="'Id'"
                                                                    :options-label="'Select One'">
                                                </kendo-dropdownlist>
                                            </div>
                                        </div>
                                        <br />
                                        <!-- County -->
                                        <div class="form-group margin-top">
                                            <label class="control-label col-lg-4 col-md-4 padding0" for="County">County</label>
                                            <div class="col-lg-8 col-md-8 padding0">
                                                <input type="text" v-model="County" name="County" class="form-control" tabindex="12" />
                                            </div>
                                        </div>
                                        <br />
                                        <div class="ActionbtnsCls">
                                            <!-- Update Button -->
                                            <input type="button" value="Save" tabindex="13" class="btn btn-default" style="margin-right: 8px;" @click="SaveLocation" />

                                            <!-- Delete Button -->
                                            <input type="button" value="Remove" :disabled="!isEditLocation" tabindex="14" class="btn btn-default" @click="DeleteLocation" />
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div>
                        <div class="content-area">
                            <a id="chooseContactButton" class="btn btn-default btn-sm m-b-sm" @click="openAddContactWin" title="Add contact." data-toggle="tooltip">
                                <i class="fa fa-plus"></i>
                                <i class="fas fa-id-card"></i>
                            </a>
                            <kendo-grid :name="testgrid"
                                        ref="locationContact"
                                        :data-source-ref="ContactsGridData"
                                        :selectable='true'
                                        :editable="'inline'"
                                        v-on:databound="onContactGridDataBound"
                                        v-on:edit="onContactGridEdit"
                                        v-on:cancel="onContactGridCancel">
                                <kendo-grid-column :field="'FullName'"
                                                   :title="'Name'">
                                </kendo-grid-column>
                                <kendo-grid-column :field="'JobTitle'"
                                                   :title="'Title'">
                                </kendo-grid-column>

                                <kendo-grid-column :field="'EmailAddress'"
                                                   :title="'Email Address'">
                                </kendo-grid-column>
                                <kendo-grid-column :title="'Main Contact For'" :headerAttributes="{'style': 'text-align: center'}" :columns="mainContactColumnsDefinitions">

                                </kendo-grid-column>
                                <kendo-grid-column width="115" :command="[
                                                                {name:'viewContact', click: this.updateContactWin,className:'p-xs'}
                                                             ]">
                                </kendo-grid-column>
                            </kendo-grid>
                        </div>
                    </div>
                    <div>
                        <location-notes ref="Comp_locationNotes"></location-notes>
                    </div>
                </kendo-tabstrip>
            </div>
        </div>
        <kendo-window ref="territoryWin"
                      :title="'Select Territory'"
                      :draggable="true"
                      :modal="true"
                      :scrollable="true"
                      :height="500"
                      :width="500"
                      :visible="false"
                      v-on:open="onOpen">
            <kendo-hierarchicaldatasource ref="remoteDataSourceComponentTreeview"
                                          :transport-read-url="GetTreeviewUrl()"
                                          :transport-read-data-type="'json'"
                                          :schema-model-id="'id'">
            </kendo-hierarchicaldatasource>
            <kendo-treeview ref="territoryTreeview"
                            data-source-ref="remoteDataSourceComponentTreeview"
                            :data-text-field="'Name'"
                            :drag-and-drop="true"
                            :auto-bind="false"
                            @select="onTerritoryTreeViewSelect">
            </kendo-treeview>
        </kendo-window>

        <kendo-window ref="NewContactWin"
                      :title="'Contact Administration'"
                      :draggable="true"
                      :modal="true"
                      :scrollable="false"
                      :height="500"
                      :width="440"
                      :visible="false">
            <div class="form-widgets">
                <div class="row m-l-none m-r-none">
                    <div class="col-lg-6 col-md-6 p-r-n p-l-n">
                        <label class="control-label col-lg-12 col-md-12 p-r-n p-l-n" for="firstName">First Name *</label>
                        <kendo-datasource ref="autoCompleteDataSource"
                                          :type="'json'"
                                          :transport-read-url="GetUrl_FirstName()"
                                          :server-filtering="true">
                        </kendo-datasource>
                        <kendo-autocomplete ref="AutoCompleteFirstName"
                                            v-model="firstName"
                                            :min-length="1"
                                            :filter="contains"
                                            style="width:95%;"
                                            :data-source-ref="'autoCompleteDataSource'"
                                            :data-text-field="'Name'">
                        </kendo-autocomplete>
                    </div>
                    <div class="col-lg-6 col-md-6 p-r-n p-l-n">
                        <label class="control-label col-lg-12 col-md-12 p-r-n p-l-n" for="lastName">Last Name *</label>
                        <input type="text" v-model="lastName" class="k-textbox" style="width:95%" />
                    </div>
                </div>

                <div class="row m-l-none m-r-none m-t-sm">
                    <div class="col-lg-6 col-md-6 p-r-n p-l-n">
                        <label class="control-label col-lg-12 col-md-12 p-r-n p-l-n" for="email">Email *</label>
                        <input type="text" v-model="email" :disabled="isEditContact" class="k-textbox" style="width:95%" />
                    </div>
                    <div class="col-lg-6 col-md-6 p-r-n p-l-n">
                        <label class="control-label col-lg-12 col-md-12 p-r-n p-l-n" for="confirm">Confirm</label>
                        <input type="text" v-model="confirm" :disabled="isEditContact" class="k-textbox" style="width:95%" />
                    </div>
                </div>

                <div class="row m-l-none m-r-none m-t-sm">
                    <div class="col-lg-6 col-md-6 p-r-n p-l-n">
                        <label class="control-label col-lg-12 col-md-12 p-r-n p-l-n" for="title">Job Title</label>
                        <input type="text" v-model="title" class="k-textbox" style="width:95%" />
                    </div>
                    <div class="col-lg-6 col-md-6 p-r-n p-l-n">
                        <label class="control-label col-lg-12 col-md-12 p-r-n p-l-n" for="employeeNbr">Employee Nbr</label>
                        <input type="text" v-model="employeeNbr" class="k-textbox" style="width:95%" />
                    </div>
                </div>

                <div class="row m-l-none m-r-none m-t-sm">
                    <div class="col-lg-6 col-md-6 p-r-n p-l-n">
                        <label class="control-label col-lg-12 col-md-12 p-r-n p-l-n" for="officeNbr">Office Phone</label>
                        <input type="text" v-model="officeNbr" class="k-textbox" style="width:95%" />
                    </div>
                    <div class="col-lg-6 col-md-6 p-r-n p-l-n">
                        <label class="control-label col-lg-12 col-md-12 p-r-n p-l-n" for="officeExt">Office Ext</label>
                        <input type="text" v-model="officeExt" class="k-textbox" style="width:50%" />
                    </div>
                </div>

                <div class="row m-l-none m-r-none m-t-sm">
                    <div class="col-lg-6 col-md-6 p-r-n p-l-n">
                        <label class="control-label col-lg-12 col-md-12 p-r-n p-l-n" for="mobilePhone">Mobile Phone</label>
                        <input type="text" v-model="mobilePhone" class="k-textbox" style="width:95%" />
                    </div>
                    <div class="col-lg-6 col-md-6 p-r-n p-l-n">
                        <label class="control-label col-lg-12 col-md-12 p-r-n p-l-n" for="homePhone">Home Phone</label>
                        <input type="text" v-model="homePhone" class="k-textbox" style="width:95%" />
                    </div>
                </div>

                <div class="row m-l-none m-r-none m-t-sm">
                    <div class="col-lg-6 col-md-6 p-r-n p-l-n">
                        <label class="control-label col-lg-12 col-md-12 p-r-n p-l-n" for="fax">Fax</label>
                        <input type="text" v-model="fax" class="k-textbox" style="width:95%" />
                    </div>
                    <div class="col-lg-6 col-md-6 p-r-n p-l-n">
                        <div class="checkbox checkbox-primary m-t-md m-l-md">
                            <input v-model="IsActive" type="checkbox" checked />
                            <label class="control-label" for="IsActive">Is Active</label>
                        </div>
                    </div>
                </div>

                <div class="row m-l-none m-r-none m-t-sm">
                    <div class="col-lg-12 col-md-12 p-r-n p-l-n">
                        <input type="button" style="float:right" class="btn btn-default" value="Save" @click="saveContact" />
                    </div>
                </div>
            </div>
        </kendo-window>
    </div>
</template>

<script>
    import axios from "axios";
    import LocationNotes from './LocationNotes';
    export default {
        name: 'location-units',
        components: {
            LocationNotes
        },
        props: {
            value: {
                required: true
            }
        },
        data() {
            return {
                TerritoryId: '',
                LocationId: '',
                LocationName: '',
                ParentName: '',
                LocationIsActive: '',
                DateClosed: '',
                Address1: '',
                Address2: '',
                City: '',
                PostalCode: '',
                Description: '',
                LocationNbr: '',
                ManagerId: '',
                WynneNbr: '',
                Lawson: '',
                StateOrProvinceId: '',
                County: '',
                ClosedByName: '',
                dataSourceArrayStates: '',
                dataSourceArrayManager: '',
                isEditLocation: false,
                isShowRemoveTerritoryHidden: false,
                schemaModelFieldsContacts: {
                    FullName: { editable: false },
                    JobTitle: { editable: false },
                    EmailAddress: { editable: false }
                },
                mainContactColumnsDefinitions: [
                    {
                        field: "VHS",
                        template: "#= 'VHS' ? '<i class=\"far fa-check-square\"></i>' : '<i class=\"far fa-square\"></i>' #",
                        filterable: false,
                        attributes: {
                            style: "text-align: center"
                        },
                        headerAttributes: {
                            style: "text-align: center"
                        }
                    },
                    {
                        field: "DRS",
                        template: "#= 'DRS' ? '<i class=\"far fa-check-square\"></i>' : '<i class=\"far fa-square\"></i>' #",
                        filterable: false,
                        attributes: {
                            style: "text-align: center"
                        },
                        headerAttributes: {
                            style: "text-align: center"
                        }
                    },
                    {
                        field: "TRS",
                        template: "#= 'TRS' ? '<i class=\"far fa-check-square\"></i>' : '<i class=\"far fa-square\"></i>' #",
                        filterable: false,
                        attributes: {
                            style: "text-align: center"
                        },
                        headerAttributes: {
                            style: "text-align: center"
                        }
                    }
                ],
                ContactsGridData: [],

                contactId:'',
                IsActive: '',
                fax: '',
                homePhone: '',
                mobilePhone: '',
                officeExt: '',
                officeNbr: '',
                employeeNbr: '',
                title: '',
                confirm: '',
                email: '',
                lastName: '',
                firstName: '',
                isEditContact: false,
            }
        },
        methods: {
            GetUrl_FirstName() {
                return Axios_Get_Url('ContactList_Read', 'User', { TerritoryId: null, LocationId: this.LocationId, Query: this.firstName }, [])
            },
            saveContact() {
                var url = "";

                if (this.contactId != "")
                    url = Axios_Get_Url('Contact_Update', 'User', {}, []);
                else
                    url = Axios_Get_Url('Contact_Create', 'User', {}, []);

                var reqFields = "";
                if (this.firstName == "")
                    reqFields += "<li>First Name</li>";
                if (this.lastName == "")
                    reqFields += "<li>Last Name</li>";
                if (this.email == "")
                    reqFields += "<li>Email Address</li>";
                if (this.email != this.confirm)
                    reqFields += "<li>Matching email confirmation</li>";

                if (reqFields != "") {
                    notify("error", "<p>The following fields are required:</p><ul>" + reqFields + "</ul>");
                }
                else {
                    var _checkUrl=Axios_Get_Url('DoesContactEmailExist', 'User', { excludeid: this.contactId != '' ? this.contactId : null, email: this.email }, []);
                    axios({ method: "GET", "url": _checkUrl }).then(result => {
                        if (result.data)
                            notify("error", "The email address you supplied already exists in our system and must be unique.");
                        else {
                            const params = new URLSearchParams();
                            params.append('Id', this.contactId);
                            params.append('TerritoryId', null);
                            params.append('LocationId', this.LocationId);
                            params.append('FirstName', this.firstName);
                            params.append('LastName', this.lastName);
                            params.append('EmailAddress', this.email);
                            params.append('JobTitle', this.title);
                            params.append('EmployeeNbr', this.employeeNbr);
                            params.append('OfficePhone', this.officeNbr);
                            params.append('OfficeExt', this.officeExt);
                            params.append('HomePhone', this.homePhone);
                            params.append('MobilePhone', this.mobilePhone);
                            params.append('Fax', this.fax);
                            params.append('IsActive', this.IsActive);

                            var app = this;
                            axios({
                                method: 'post',
                                url: url,
                                data: params
                            }).then(function (response) {
                                if (response.data.Success) {
                                    app.clearContact();
                                    app.$refs.NewContactWin.kendoWidget().close();
                                    app.LoadContactsGridData();
                                    app.ManagerDrpDwnRead();
                                }
                                else {
                                    notify("error", response.data.ErrorText);
                                    handleError(response.data.ErrorText);
                                }
                            }).catch(function (error) {
                                console.error(error);
                            });
                        }
                    }, error => {
                        console.error(error);
                    });
                }
            },
            clearContact() {
                this.contactId=''
                this.firstName = '';
                this.lastName  = '';
                this.email = '';
                this.confirm = '';
                this.title = '';
                this.employeeNbr = '';
                this.officeNbr = '';
                this.officeExt = '';
                this.mobilePhone = '';
                this.homePhone = '';
                this.fax = '';
                this.IsActive = '';
                this.isEditContact = false;

                var autoComplete = this.$refs.AutoCompleteFirstName.kendoWidget();
                autoComplete.unbind("open");
                autoComplete.suggest = true; // enable autocomplete suggestions
            },
            onFirstNameSelect() {

            },
            updateContactWin(e) {
                alert("Update");
                var app = this;
                var grid = this.$refs.locationContact.kendoWidget();
                var dataItem = grid.dataItem($(e.currentTarget).closest("tr"));
                
                //var dataRow = $(e.currentTarget).closest("tr");
                //var dataItem = dataRow != null ? e.sender.dataItem(dataRow) : null;
                console.log(dataItem);
                if (dataItem) {
                    var _url = Axios_Get_Url('ContactDetails_Read', 'User', { ContactId: dataItem.Id }, []);
                    axios({ method: "GET", "url": _url }).then(result => {
                        app.openAddContactWin();
                        var obj = $.parseJSON(JSON.stringify(result.data));

                        this.contactId = obj[0].Id;
                        this.firstName = obj[0].FirstName;
                        this.lastName = obj[0].LastName;
                        this.email =  obj[0].EmailAddress;
                        this.confirm = obj[0].EmailAddress;
                        this.title = obj[0].JobTitle;
                        this.employeeNbr =obj[0].EmployeeNbr;
                        this.officeNbr = obj[0].OfficePhone;
                        this.officeExt = obj[0].OfficeExt;
                        this.mobilePhone = obj[0].MobilePhone;
                        this.homePhone = obj[0].HomePhone;
                        this.fax = obj[0].Fax;
                        this.IsActive = obj[0].IsActive;

                        if (obj[0].HasUserRecord) {
                            this.confirm = obj[0].EmailAddress;
                            this.isEditContact = true;
                            //$("#email").attr("disabled", "disabled");
                        }

                        var autoComplete = app.$refs.AutoCompleteFirstName.kendoWidget();
                        autoComplete.suggest = false; // disable autocomplete suggestions
                        autoComplete.bind("open", function (e) {
                            e.preventDefault();
                        });
                    }, error => {
                        console.error(error);
                    });
                }
            },
            onContactGridDataBound() {

            },
            onContactGridEdit() {

            },
            onContactGridCancel() {

            },
            GetReadUrl_Contacts() {
                return Axios_Get_Url('ContactDetailsByLocation_Read', 'User', { LocationId: this.LocationId }, []);
            },
            GetUpdateUrl_Contacts() {
                return Axios_Get_Url('LocationContact_Update', 'User', {}, []);
            },
            GetDestroyUrl_Contacts() {
                return Axios_Get_Url('LocationContact_Delete', 'User', {}, []);
            },
            close() {
                this.ResetLocationForm();
                this.$emit("input", false);
            },
            GetTreeviewUrl() {
                return Axios_Get_Url('Territory_Read', 'Territory', {}, []);
            },
            onTerritoryTreeViewSelect(e) {
                var data = e.sender.dataItem(e.node);
                this.TerritoryId = data.id;
                this.ParentName = data.Name;
                this.$refs.territoryWin.kendoWidget().close();
            },
            openTerritoryWin() {
                this.$refs.territoryWin.kendoWidget().open().center();
                this.$refs.territoryTreeview.kendoWidget().dataSource.read();
            },
            removeFromTerritory() {
                this.TerritoryId = '';
                this.ParentName = 'None';
            },
            GetUrl_NewContactWin() {
                return Axios_Get_Url('addContactWinForm', 'User', {}, []);
            },
            openAddContactWin() {
                this.$refs.NewContactWin.kendoWidget().open().center();
            },
            ResetLocationForm() {
                this.TerritoryId = '';
                this.LocationName = '';
                this.ParentName = '';
                this.LocationIsActive = false;
                this.DateClosed = '';
                this.Address1 = '';
                this.Address2 = '';
                this.City = '';
                this.PostalCode = '';
                this.Description = '';
                this.LocationNbr = '';
                this.ManagerId = '';
                this.WynneNbr = '';
                this.Lawson = '';
                this.StateOrProvinceId = '';
                this.County = '';
                this.ClosedByName = '';
                this.isEditLocation = false;
                this.$refs.drpdwnManager.kendoWidget().enable(false);
            },
            LoadContactsGridData() {
                var _url = Axios_Get_Url('ContactDetailsByLocation_Read_Vue', 'User', { LocationId: this.LocationId }, []);
                axios({ method: "GET", "url": _url }).then(result => {
                    this.ContactsGridData = result.data;
                }, error => {
                    console.error(error);
                });
            },
            ManagerDrpDwnRead() {
                var _urlManagerRead = Axios_Get_Url('ContactList_Read', 'User', { LocationId: this.LocationId, TerritoryId: null }, []);
                axios({ method: "GET", "url": _urlManagerRead }).then(result => {
                    this.dataSourceArrayManager = result.data;
                }, error => {
                    console.error(error);
                });
            },
            refreshUnits(locationId) {
                this.$refs.Comp_locationNotes.setDefaultValues(locationId);
                var app = this;
                this.LocationId = locationId;
                var _url = Axios_Get_Url('GetLocationDetails', 'Territory', { LocationId: locationId }, ['TenantId']);
                axios({ method: "GET", "url": _url }).then(result => {
                    console.log(result.data);
                    this.TerritoryId = result.data.TerritoryId;
                    this.LocationName = result.data.Name;
                    this.ParentName = result.data.TerritoryId == null ? "None" : result.data.ParentName;
                    this.LocationIsActive = result.data.IsActive;
                    this.DateClosed = result.data.DateClosed;
                    this.Address1 = result.data.Address1;
                    this.Address2 = result.data.Address2;
                    this.City = result.data.City;
                    this.PostalCode = result.data.PostalCode;
                    this.Description = result.data.Description;
                    this.LocationNbr = result.data.LocationNbr;
                    this.ManagerId = result.data.ManagerId;
                    this.WynneNbr = result.data.Wynne;
                    this.Lawson = result.data.Lawson;
                    this.StateOrProvinceId = result.data.StateOrProvinceId;
                    this.County = result.data.County;
                    this.ClosedByName = result.data.ClosedByName;
                    this.isShowRemoveTerritoryHidden = result.data.TerritoryId;
                    this.$refs.drpdwnManager.kendoWidget().enable(true);

                    //-Read Contact grid
                    app.LoadContactsGridData();

                }, error => {
                    console.error(error);
                });

                //--Manager Dropdown data read
                app.ManagerDrpDwnRead();
            },
            SaveLocation() {
                var action = this.isEditLocation ? 'Update_Location' : 'Create_Location';
                var actionURL = Axios_Get_Url(action, 'Territory', {}, []);
                var reqFields = "";
                if (this.LocationName == "")
                    reqFields += "<li>Name</li>";

                if (reqFields != "")
                    notify("error", "<p>The following fields are required:</p><ul>" + reqFields + "</ul>");
                else {
                    if (this.TerritoryId == null || this.TerritoryId == '') {
                        var _urlTenant = Axios_Get_Url('TenantType_Read', 'Tenant', {}, ['TenantId']);
                        axios({ method: "GET", "url": _urlTenant }).then(result => {
                            if (result.data == 1)
                                notify("error", "Locations cannot be added directly to a company designated as a service provider.");
                            else
                                this.completeSave(actionURL);
                        }, error => {
                            console.error(error);
                        });
                    }
                    else
                        this.completeSave(actionURL);
                }
            },
            completeSave(actionURL) {
                const params = new URLSearchParams();
                params.append('Id', this.LocationId);
                params.append('TenantId', func_getTenantId());
                params.append('Name', this.LocationName);
                params.append('LocationNbr', this.LocationNbr);
                params.append('IsActive', this.LocationIsActive);
                params.append('TerritoryId', this.TerritoryId);
                params.append('ManagerId', this.ManagerId);
                params.append('Wynne', this.WynneNbr);
                params.append('Lawson', this.Lawson);
                params.append('DateClosed', this.DateClosed ? this.DateClosed.getMonth() + '/' + this.DateClosed.getDate() + '/' + this.DateClosed.getFullYear() : null);
                params.append('StateOrProvinceId', this.StateOrProvinceId);
                params.append('County', this.County);
                params.append('City', this.City);
                params.append('PostalCode', this.PostalCode);
                params.append('Description', this.Description);
                params.append('Address1', this.Address1);
                params.append('Address2', this.Address2);

                var app = this;
                axios({
                    method: 'post',
                    url: actionURL,
                    data: params
                }).then(function (response) {
                    if (response.data.Success) {
                        if (app.isEditLocation) {
                            notify("success", "Changes to " + app.LocationName + " were saved.");
                        }
                        else {
                            app.LocationId = response.data.newLocationId;
                            app.ResetLocationForm();
                            notify("success", "New location was added.");
                        }
                        app.close();
                        app.$emit("LocationUpdated", true);
                    }
                    else {
                        notify("error", response.data.ErrorText);
                        handleError(response.data.ErrorText);
                    }
                }).catch(function (error) {
                    console.error(error);
                });
            },
            DeleteLocation() {
                var app = this;
                if (confirm('You are about to permanently delete the selected location. Are you sure?')) {
                    var _urlDeleteLocation = Axios_Get_Url('Delete_Location', 'Territory', {}, []);

                    const params = new URLSearchParams();
                    params.append('Id', this.LocationId);

                    axios({
                        method: 'post',
                        url: _urlDeleteLocation,
                        data: params
                    }).then(function (response) {
                        if (response.data.Success) {
                            notify("success", "Location was deleted from database.");
                            app.$emit("LocationUpdated", true);
                            app.close();
                        }
                        else {
                            notify("error", response.data.ErrorText);
                            handleError(response.data.ErrorText);
                        }
                    }).catch(function (error) {
                        console.error(error);
                    });
                }
            }
        },
        mounted() {

            //--States Dropdown data read
            var _urlStatesRead = Axios_Get_Url('States_Read', 'Lookup', {}, []);
            axios({ method: "GET", "url": _urlStatesRead }).then(result => {
                this.dataSourceArrayStates = result.data;
            }, error => {
                console.error(error);
            });


            var _url = Axios_Get_Url_API('GetLocation', 'Location', { tenantId: func_getTenantId(), userId: func_getUserId() });
            axios({ method: "GET", "url": _url }).then(result => {
                console.log(result);
                this.gridData = result.data;
            }, error => {
                console.error(error);
            });
        }
    }
</script>
